.container-error {
    position: relative;
    height: 100vh;
}

.error {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.error__main {
    position: relative;
    margin-bottom: rem(33px);
    width: rem(406px);
    display: flex;
    justify-content: center;

    @include phones() {
        width: 300px;
    }
}

.title-error__sumbol {
    margin: 0;
}

.error__img {
    width: rem(160px);
    height: rem(255px);
    top: 50%;
    left: 50%;
}

.error__title {
    position: absolute;
    top: 50%;
    display: flex;
    transform: translateY(-50%);
    justify-content: space-between;
    width: 100%;
    font-weight: 500;
    font-size: rem(120px);
    line-height: rem(144px);
    color: #B1B8CB;
    margin: 0;

    @include phones() {
        font-size: rem(100px);
    }
}

.error__text {
    text-align: center;

    span {
        display: block;
        white-space: nowrap;
        font-weight: normal;
        font-size: rem(18px);
        line-height: rem(22px);
        color: #000000;
    }
}

.error__subtitle {
    font-weight: bold;
    font-size: rem(32px);
    line-height: rem(38px);
    color: #283345;
    margin-bottom: rem(10px);
}