.form-card {
    margin-bottom: rem(35px);
}

.form-card__item {
    display: flex;
    align-items: center;
    margin-bottom: rem(19px);

    &:last-child {
        margin-bottom: 0;
    }
}

.form-card__label-color {
    line-height: rem(19px);
    margin-right: rem(15px);
    font-size: rem(16px);
    color: #B1B8CB;
}

.options-card {
    display: flex;
}

.options-card__radio {
    display: none;
}

.options-card__radio:checked+.form-card__label::after {
    transform: scale(1);
}


.form-card__label {
    display: flex;
    position: relative;
    cursor: pointer;

    &::before {
        content: '';
        height: rem(22px);
        width: rem(22px);
        margin-right: rem(14px);
    }

    &::after {
        transition: all .3s ease;
        content: '';
        border-bottom: 2px solid $brown;
        position: absolute;
        top: 120%;
        left: 0;
        right: 0;
        width: rem(22px);
        transform: scale(0);
    }

    &--1::before {
        background: #C4C4C4;
    }

    &--2::before {
        background: #EAB4B2;
    }

    &--3::before {
        background: #5A4570;
    }
}

.form-card__size {
    margin-right: rem(26px);
    line-height: rem(19px);
    margin-right: rem(15px);
    font-size: rem(16px);
    color: #B1B8CB;
}

.options-card__radio:checked+.form-card__label-size span::after {
    transform: scale(1);
}

.form-card__label-size {
    display: flex;
    position: relative;
    cursor: pointer;
    margin-right: rem(10px);
    padding: 0 rem(3px);

    span {
        line-height: rem(19px);
        display: flex;
        font-size: rem(16px);
        color: #283345;

        &::after {
            transition: all .3s ease;
            content: '';
            border-bottom: 2px solid $brown;
            position: absolute;
            top: 120%;
            left: 0;
            right: 0;
            transform: scale(0);
        }
    }
}

.counter {
    display: inline-flex;
    margin-right: rem(15px);
}

.counter__area {
    display: flex;
    position: relative;
}

.counter__controls {
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: space-between;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 rem(18px);
}

.counter__input {
    border: none;
    padding: rem(20px) rem(20px);
    text-align: center;
    width: rem(130px);
    height: rem(50px);
    border-radius: 100px;
    position: relative;
    border: 1px solid #E0E3EA;
}

.counter__btn {
    background: #fff;
    color: rgba(0, 0, 0, 0.4);
    line-height: rem(25px);
    border: none;
    cursor: pointer;
    outline: none;

    &:last-child {
        margin-bottom: 0;
    }
}

.form-card__btn {
    display: inline-flex;
}