.another-product {
    margin-bottom: rem(100px);
}

.card-product {
    margin-bottom: rem(67px);
}

.card-product__container {
    @include tablets() {
        width: 95%;
        max-width: rem(520px);
        margin: 0 auto;
    }
}

.card-product__content {
    display: flex;
    justify-content: space-between;
    margin-bottom: rem(59px);

    @include tablets() {
        flex-direction: column;
    }
}

.card-product__list {
    min-width: 0;
}

.card-product__column {
    flex: 0 0 43%;
    min-width: 0;
    position: relative;

    &:last-child {
        margin-right: 0;
    }
}

.desc-card {
    flex: 0 0 47%;
}



.card-product__list {
    margin-bottom: rem(30px);
}

.card-bg__item {
    display: flex;
    justify-content: center;
}

.card-sm__item {
    display: flex;
    min-width: 0;
    justify-content: center;
    opacity: 0.4;
    transition: all .3s ease;
    min-width: rem(150px);
    width: 100%;
}

.card-sm__item.slick-center {
    opacity: 1;
}

.card-sm__img {
    width: 90%;
}

.card-bg__img {
    max-height: rem(640px);

    @include tablets() {
        max-height: rem(630px);
    }

    @include phones() {
        max-height: rem(500px);
    }
}


.card-sm {
    padding: 0 rem(5px);
    max-width: rem(480px);
    margin: 0 auto;


    @include tablets() {
        margin-bottom: rem(21px);

    }

    .slick-arrow {
        position: absolute;
        z-index: 100;
        font-size: 0;
        cursor: pointer;
    }

    .slick-prev {
        left: 0;
    }

    .slick-next {
        right: 0;
    }
}

.card-product__controls {
    position: absolute;
    top: 38%;
    display: flex;
    justify-content: space-between;

    .slick-arrow {
        position: relative;
        background-color: inherit;
        opacity: 1;

        &::after {
            content: "";
            width: rem(20px);
            height: rem(20px);
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            position: absolute;
            background-image: url("data:image/svg+xml;utf8,<svg viewBox='0 0 11 7' xmlns='http://www.w3.org/2000/svg'><path d='M9.85975 1.54027C10.0597 1.35027 10.0597 1.04027 9.85975 0.860273L9.13978 0.180281C8.93978 -0.00971924 8.61975 -0.00971924 8.42975 0.180281L5.02976 3.47026L1.59977 0.150312C1.39977 -0.0396875 1.07977 -0.0396875 0.879767 0.150312L0.159766 0.830305C-0.0402344 1.02031 -0.0402344 1.3203 0.159766 1.5103L4.66978 5.86027C4.86978 6.05027 5.18978 6.05027 5.38978 5.86027L9.85975 1.54027Z' fill='%23B1B8CB'/></svg>");
            opacity: 1;
            background-position: center center;
            background-repeat: no-repeat;
            background-color: transparent;
            border-radius: 0;
        }
    }
}