.breadcrumb {
    min-height: rem(55px);
    background: #F4F5FB;
    display: flex;
    align-items: center;
    margin-bottom: rem(33px);
    position: relative;
}

.decoration {
    position: absolute;
    top: -36%;
    width: 100%;
    left: 0;
    right: 0;
    fill: #fff;
}

.decoration-banner {
    top: -2%;
    z-index: 1000;

    &--bottom {
        top: 98%;
    }

    .decoration__icon {
        width: (2000px);
    }
}

.decoration__icon {
    height: rem(30px);
    overflow: hidden;
    width: rem(1700px);
}

.decoration-video {
    top: -3%;
    z-index: 1000;

    &--bottom {
        top: 97%;
    }
}

.breadcrumb__list {
    font-size: rem(15px);
    line-height: rem(18px);
    color: rgba(0, 0, 0, 0.4);
    display: flex;
    flex-wrap: wrap;
}

.breadcrumb__item {
    padding-left: rem(10px);
    padding-right: rem(10px);
    position: relative;

    &:hover {
        transition: all .3s ease;
        color: $darkBrown;

        &::before {
            color: rgba(0, 0, 0, 0.4);
        }
    }

    &::before {
        display: block;
        content: "/";
        position: absolute;
        top: 0;
        right: 0;
    }

    &:last-child::before {
        display: none;
    }
}