.banner {
    margin-bottom: rem(67px);
    position: relative;
}

.banner__wrap {
    position: relative;
    background: url("../img/main/slider.png") 17% center/ cover no-repeat;
    min-height: rem(650px);
    color: #FFFFFF;
    position: relative;
}

.banner__wrap--video {
    background: url("../img/main/banner.png") left center/ cover no-repeat;
    background-position: left 70% center;
    min-height: rem(400px);
    background-color: #F1ECE8;
    position: relative;

    &::before {
        content: '';
        transform: translateX(-50%);
        position: absolute;
        display: block;
        top: 0;
        left: 31%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, #F1ECE8 0%, rgba(241, 236, 232, 0) 100%);
    }

    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        height: 100%;
        width: 19%;
        background: linear-gradient(90deg, #F1ECE8 0%, rgba(241, 236, 232, 0) 100%);
        transform: matrix(-1, 0, 0, 1, 0, 0);
    }
}

.desc-video {
    padding: 84px 0;
    display: flex;
}

.desc-video__content {
    padding: 54px 40px;
    position: relative;
    max-width: 55%;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background: #FFFFFF;
        opacity: 0.5;
        border-radius: 15px;
    }

    @include tablets() {
        max-width: 70%;
    }

    @include phones() {
        max-width: 80%;
    }
}

.banner__title {
    font-size: rem(44px);
    line-height: rem(55px);
    margin-bottom: rem(31px);
    position: relative;
    font-weight: bold;
    color: #283345;

    &--video {
        margin-bottom: rem(26px);
    }
}

.banner__btn {
    position: relative;
}

.banner__text {
    margin-bottom: rem(75px);
    display: block;
    line-height: rem(26px);
    position: relative;
    font-size: rem(20px);
    color: #B1B8CB;

    &--video {
        margin-bottom: rem(3px);
        opacity: .6;
    }
}


.banner__desc {
    position: relative;
    display: flex;
    justify-content: flex-end;
    padding-top: rem(139px);
    padding-bottom: rem(150px);

    &--video {
        padding-top: rem(138px);
        flex-direction: column;
    }


    @include phones() {
        padding-bottom: rem(80px);
        padding-top: rem(80px);
    }
}

.banner__desc-content {
    width: 46%;
    position: relative;
    padding: rem(36px) rem(50px);

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background: #FFFFFF;
        opacity: 0.5;
        border-radius: 15px;
        width: 100%;
        height: 100%;
    }

    @include tablets() {
        width: 55%;
    }

    @include phones() {
        width: 72%;
    }
}

// .slider__list

.slider {
    position: relative;
}

.slick-track {
    display: flex;
}

.slick-list {
    overflow: hidden;
}

.slick-list .slick-track {
    display: flex;
    align-items: flex-start;
}

.slider__list .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
}

.slider__list .slick-dots li {
    margin-right: rem(10px);
    display: flex;
    border-radius: 50%;

    &:last-child {
        margin-right: 0;
    }
}

.slider__list .slick-dots button {
    font-size: 0;
    width: 10px;
    height: 10px;
    background-color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 0;
    border-radius: 50%;
}

.slider__list .slick-dots .slick-active {
    button {
        background-color: $brown;
    }
}

.slider__item {
    height: 100%;
    position: relative;
}